@import url(https://fonts.googleapis.com/css2?family=Ubuntu:ital,wght@0,300;0,400;0,500;0,700;1,300;1,400;1,500;1,700&display=swap);
.field {
	font-size: 2.5rem;
	color: rgba(0, 0, 0, 0.8);
	display: block;
	outline: none;
	border: 0.5px solid black;
	padding: 0.5em;
	-webkit-flex-grow: 1;
	        flex-grow: 1;
	margin-left: 0.5em;
	border-radius: 10px;
}

.label {
	color: rgba(0, 0, 0, 0.8);
	font-size: 2rem;
	display: -webkit-flex;
	display: flex;
	-webkit-align-items: center;
	        align-items: center;
	padding: 0.5em;
}

.btn {
	font-size: 2rem;
	padding: 0.5em 2.5em;
	border-radius: 5px;
	outline: none;
	cursor: pointer;
	border: 1px solid black;
	color: rgba(0, 0, 0, 0.8);
}

.center {
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: center;
	        justify-content: center;
}

.task-list {
	padding: 2em;
	color: rgba(0, 0, 0, 0.7);
}

.task-list-title {
	font-size: 3rem;
	margin: 0px;
	text-align: center;
}

.task-list-edit-hint {
	margin: 0;
	text-align: center;
	color: rgba(0, 0, 0, 0.3);
}

.task-container {
	padding: 2.5em;
	background-color: rgba(255, 255, 255, 0.1);
	border: 1px solid rgba(0, 0, 0, 0.1);
	color: rgba(0, 0, 0, 0.7);
	border-radius: 10px;
	display: -webkit-flex;
	display: flex;
	-webkit-flex-direction: column;
	        flex-direction: column;
	cursor: pointer;
	transition: box-shadow 0.3s linear;
	-webkit-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.task-container:not(:first-child) {
	margin-top: 1em;
}

.task-container:hover {
	box-shadow: -3px 13px 24px -3px rgba(0, 0, 0, 0.52);
}

.task-title {
	font-size: 3rem;
	margin: 0px;
	position: relative;
	-webkit-align-self: center;
	        align-self: center;
}

.task-title::after {
	content: "";
	position: absolute;
	border-top: 0.1px solid rgba(0, 0, 0, 0.5);
	width: 50%;
	display: block;
	margin: auto;
	left: 0;
	right: 0;
	bottom: 2px;
}

.task {
	font-size: 2rem;
	font-weight: 700;
	margin-bottom: 0px;
}

.task > span {
	display: block;
	font-size: 1.5rem;
	font-weight: 400;
	line-height: 1.1;
	letter-spacing: 1px;
	word-wrap: break-word;
}

/*:root{
}*/

*::before,
*::after,
* {
	box-sizing: border-box;
}

body,
html {
	padding: 0%;
	margin: 0%;
	height: 100%;
	font-size: 10px;
	font-family: "Ubuntu", sans-serif;
	padding: 1em;
}

.root {
	max-width: 1200px;
	margin: auto;
	border: 1px solid rgba(0, 0, 0, 0.5);
}

.line {
	border: 1px solid rgba(0, 0, 0, 0.5);
}

